import Vue from 'vue'
import axios from 'axios'
import { Promise } from "core-js"
import adapter from 'axios-miniprogram-adapter'
// import {Notify} from '../wxcomponents/dist/notify/notify'
// 
const request = axios.create({
    // VUE_APP_BASE_APIvue.config.js
    // baseURL: process.env.VUE_APP_BASE_API,
    // 
    baseURL: 'http://shop-api.edu.koobietech.com',
    // axioshttpvue.config.js
    // axios-miniprogram-adapteraxios
    // axios
    adapter: adapter,
})
axios.defaults.adapter = function(config) {
    return new Promise((resolve, reject) => {
        console.log(config)
        var settle = require('axios/lib/core/settle');
        var buildURL = require('axios/lib/helpers/buildURL');
        uni.request({
            method: config.method.toUpperCase(),
            url: config.baseURL + buildURL(config.url, config.params, config.paramsSerializer),
            header: config.headers,
            data: config.data,
            dataType: config.dataType,
            responseType: config.responseType,
            sslVerify: config.sslVerify,
            complete: function complete(response) {
                response = {
                    data: response.data,
                    status: response.statusCode,
                    errMsg: response.errMsg,
                    header: response.header,
                    config: config
                }
                settle(resolve, reject, response);
            }
        })
    })
}


// request,
request.interceptors.request.use(
	config => {
		const token = uni.setStorageSync("token")
		// console.log(config, '')
		return config;
	},
	error => {
		return Promise.reject(error);
	}
);

//
request.interceptors.response.use(res => {
	const data = res.data;
	if (data.code == "00000") {
		return data.data
	}

	if (data.code === "A00004") {
		// Notify('!')
		uni.navigateTo({
			url:"/pages/login/login"
		})
		return Promise.reject("!")
	}
	// Notify(data.msg)
	return Promise.reject(data.msg)
}, error => {

	return Promise.reject(error)
})




export default request
