import service from "@/utils/request.js";

// 
export function confirm(data){
	return service({
		url:"/p/order/confirm",
		method:"post",
		data:data
	})
}

//  
export function submit(data){
	return service({
		url:"/p/order/submit",
		method:"post",
		data:data
	})
}

// 
export function pay(data){
	return service({
		url:"/p/order/pay",
		method:"post",
		data:data
	})
}

// 
export function orderCount(){
	return service({
		url:"/p/myOrder/orderCount",
		method:"get"
	})
}